import { Component, OnInit } from '@angular/core';
import { LocationsService, Location } from "../../../generated/ports-locations/index";
import { Router } from "@angular/router";

@Component({
  selector: 'app-ports',
  templateUrl: './ports.component.html',
  styleUrls: ['./ports.component.css'],
  providers: [LocationsService]
})
export class PortsComponent implements OnInit {

  ngOnInit(): void {
    this.getPorts();
  }
  constructor(
    private router: Router,
    private portsService: LocationsService
  ) { }
  ports: Location[];

  getPorts(): void {
    this.portsService.getLocationsUsingGET().toPromise()
      .then(ports => this.ports = ports)
      .catch(error => console.log(error));
  }
  gotoDetail(selectedLocation: Location): void {
    this.router.navigate(['/detail', selectedLocation.mmxId]);
  }
}



