import { Component, OnInit } from '@angular/core';
import { LocationsService, Location, DistancesService } from "../../../generated/ports-locations/index";
import 'rxjs/add/operator/toPromise';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-distance-matrix',
  templateUrl: './distance-matrix.component.html',
  styleUrls: ['./distance-matrix.component.css']
})
export class DistanceMatrixComponent implements OnInit {

  private data: any = new Array();
  private colHeaders: string[] = new Array();
  private columns: any[];
  private options: any;
  private locations: Location[] = new Array();
  private distances: any;
  private version: string;

  // name --> mmxId
  private locationMap: Map<string, string> = new Map<string, string>();

  constructor(
    private route: ActivatedRoute,
    private locationsService: LocationsService,
    private distancesService: DistancesService
  ) {
    if (route.snapshot.paramMap.has("version")){
      this.version = route.snapshot.paramMap.get("version");
    }
    this.options = {
      height: 700,
      rowHeaders: new Array(),
      rowHeaderWidth: 180,
      stretchH: 'all',
      columnSorting: true,
      contextMenu: true,
      className: 'htCenter htMiddle',
      afterChange: (changes) => {
        if (changes == null){
          // prevent update calls when building table
          return
        }
        this.updateDistance(
          this.locationForRow(changes[0][0]),
          this.locationForColumn(changes[0][1]),
          changes[0][3]
        )
      }
    };
   }

  ngOnInit() {
    this.fetchUpstream()
  }

  private fetchUpstream(){
    this.locationsService.getLocationsUsingGET(this.version, undefined).toPromise()
    .then(locationns => {
      this.locations = locationns.filter(l => l.virtual == false)
      this.data = new Array(this.locations.length)
      for (var _i = 0; _i < this.data.length; _i++) {
        this.data[_i] = new Array(this.locations.length)
    }
      this.locations.filter(l => l.virtual == false).forEach(l => {
        this.locationMap.set(l.name, l.mmxId)
        this.colHeaders.push(l.name)
        this.options.rowHeaders.push(l.name)
      })
    })
    .catch(error => console.log(error));

    this.distancesService.getDistanceMatrixUsingGET(undefined, this.version, undefined).toPromise()
    .then(distances => {
      this.distances = distances;
      for (var i = 0; i < this.colHeaders.length; i++) {
        this.data[i] = new Array()
        for (var j = 0; j < this.colHeaders.length; j++) {
          this.data[i][j] = this.distances[this.locationMap.get(this.options.rowHeaders[i])][this.locationMap.get(this.colHeaders[j])]
        }
      }
    })
  }

  private locationForRow(row: Number): Location{
    return this.locations.find(p => p.name === this.options.rowHeaders[row.toString()])
  }

  private locationForColumn(column: Number): Location{
    return this.locations.find(p => p.name === this.colHeaders[column.toString()])
  }

  private updateDistance(from: Location, to: Location, distance: number){
    this.distancesService.updateDistanceUsingPUT(from.mmxId, to.mmxId, {distance: distance, provider: "Web UI"}).toPromise()
      .then(result => console.log("successfully updated"))
      .catch(error => console.log(error))
    console.log(`the update: ${from.name} -> ${to.name}: ${distance}`);
  }

  private filterColumn(searchString: string){
    let filteredLocations = this.locations.filter(p => p.name.toLowerCase().startsWith(searchString.toLowerCase()))
    // let filteredColHeaders = this.colHeaders.filter(e => e.startsWith(searchString))
    let filteredData = new Array()
    
    for (var i = 0; i < this.options.rowHeaders.length; i++) {
      filteredData[i] = new Array()
      for (var j = 0; j < filteredLocations.length; j++) {
        filteredData[i][j] = this.distances[this.locationMap.get(this.options.rowHeaders[i])][filteredLocations[j].mmxId]
      }
    }
    this.colHeaders = filteredLocations.map(p => p.name)
    this.data = filteredData
    this.options = Object.assign({}, this.options)
  }

  private filterRow(searchString: string){
    let filteredLocations = this.locations.filter(p => p.name.toLowerCase().startsWith(searchString.toLowerCase()))
    // let filteredColHeaders = this.colHeaders.filter(e => e.startsWith(searchString))
    let filteredData = new Array()
    
    for (var i = 0; i < filteredLocations.length; i++) {
      filteredData[i] = new Array()
      for (var j = 0; j < this.colHeaders.length; j++) {
        filteredData[i][j] = this.distances[filteredLocations[i].mmxId][this.locationMap.get(this.colHeaders[j])]
      }
    }
    this.options.rowHeaders = filteredLocations.map(p => p.name)
    this.data = filteredData
    this.options = Object.assign({}, this.options)
  }

  fromChange(seachString: string){
    this.filterRow(seachString)
  }

  toChange(seachString: string){
    this.filterColumn(seachString)
  }
}



